<script>
  import Event from "./Event.svelte";
  import _ from 'lodash';
  export let calendar;
  export let selectDate;

  let appDate = {};
  let calDates = calendar.calDates[calendar.selectedLocation['city_id']];
  let filter = '';

  function setUpcoming() {
    let keys = Object.keys(calDates),
      i = -1, count = 0;

    appDate = _.cloneDeep(calendar.selectedDate);

    while (count < 60) {
      incrementDay();
      i = keys.indexOf(appDate.year + '-' + ('0' + (appDate.month + 1)).slice(-2) + '-' + ('0' +
        appDate.date).slice(-2));
      if (i > -1) break;
      count++;
    }

    if (!(count >= 59 && i < 0)) {
      while (i < keys.length && calendar.upcoming.length < 7) {
        let day = calDates[keys[i]];
        if (day) calendar.upcoming.push(day.dateText);
        i++;
      }
    }
  }

  function incrementDay() {
    if (appDate.date < calendar.months[appDate.month].days) {
      appDate.date++;
    } else {
      appDate.date = 1;
      if (appDate.month < 11) {
        appDate.month++
      } else {
        appDate.month = 0;
        appDate.year++;
      }
    }
  }

  function filterDates(){
    if (filter) {
      calendar.filtered = {};

      _.each(calDates, date => {
        if (date.events && date.events.length) {
          _.each(date.events, ev => {
            if (ev.name.toLowerCase().includes(filter.toLowerCase())) {
              calendar.filtered[date.dateText] = date;
            };
          });
        }
      });
    } else {
      calendar.filtered = calDates;
    }
}

  setUpcoming();
</script>
<div class="events">
  <div class="row events-options margin4 cross-center">
    {#if !calendar.isDateSelected}
      <div class="cancel clickable w-15 h-100 row main-center cross-center" on:click={ () => { calendar.isDateSelected = true } }>
        &#9932;
      </div>
    {:else}
      <div class="w-15 clickable h-100 row cross-center main-center" on:click={() => selectDate(calendar.today)} title="Go to Today">
        &#8634;
      </div>
    {/if}
    <input type="text" bind:value={ filter } id="search" class="flex search" placeholder="Search event" on:click={() => calendar.isDateSelected = false } on:input={ filterDates }>
    <div class="w-15 clickable h-100 row cross-center main-center" on:click={ () => { calendar.isDateSelected = false; calendar.filtered = calDates; } } title="View all events">
      &#9776;
    </div>
  </div>
  <div class="list-container">
    {#if calendar.isDateSelected && calendar.selectedDate.dateText}
      <Event selectDate={ selectDate } calendar={calendar} day={calDates[calendar.selectedDate.dateText]} />
      {#if calendar.selectedDate.dateText === calendar.today.dateText}
        <div class="fw-bold upcoming row">Upcoming Events</div>
        {#each calendar.upcoming as dateText}
          <Event selectDate={ selectDate } calendar={ calendar } day={
            calDates[dateText]
          } />
        {/each}
      {/if}
    {:else}
      {#each Object.entries(calendar.filtered) as [dateText, day]}
        <Event selectDate={ selectDate } calendar={ calendar } day={ day } />
      {/each}
    {/if}
  </div>
</div>
<style lang="scss">
  @import '../assets/css/app.scss';

  $all:('', 0, 4000px);
  $sm:('-sm', 0, 767px);
  $md:('-md', 768px, 959px);
  $lg:('-lg', 960px, 4000px);

  $breakpoints: ($all, $sm, $md, $lg);
  @mixin sm {
    @media screen and (min-width: nth($sm, 2)) and (max-width: nth($sm, 3)) {
      @content;
    }
  }

  @mixin md {
    @media screen and (min-width: nth($md, 2)) and (max-width: nth($md, 3)) {
      @content;
    }
  }

  @mixin lg {
    @media screen and (min-width: nth($lg, 2)) and (max-width: nth($lg, 3)) {
      @content;
    }
  }

  .events {
    min-width: 200px;
    overflow: visible;

    .list {
      margin: 20px 0 10px 5px;
      border-radius: 3px;
      overflow: hidden;
    }

    .upcoming {
      font-size: 20px;
      margin-top: 50px;
      border-bottom: 1px solid grey;
    }
  }

  .list-container {
    @include lg {
      max-height: calc(100vh - 123px);
    }

    @include md {
      max-height: calc(100vh - 191px);
    }
  }

  // tablet + desktop
  @media screen and (min-width: 768px) {
    .events {
      width: 35%;
    }

    .list-container {
      overflow-y: auto;
      padding-right: 5px;
    }

    .events-options {
      height: 35px;
    }

    .search {
      height: 29px;
    }
  }

  // mobile
  @media screen and (max-width: 767px) {
    .events {
      width: 100%;
      margin-top: 10px;
    }

    .events-options {
      height: 45px;
    }

    .search {
      height: 40px;
      padding: 0 10px;
      border-radius: 5px;
    }
  }
</style>