<script>
  import _ from 'lodash';
	import cities from '../assets/js/cities';
	import Events from '../components/Events.svelte';
	import CalLocationSelect from '../components/CalLocationSelect.svelte';

	let calendar = {
		weeks: [],
		day1Index: 0,
		dayLastIndex: 0,
		index: 0,
		year: 0,
		today: {},
		selectedDate: {},
		selectedLocation: { name: 'Select Location...' },
		calDates: {},
		isDateSelected: true,
    upcoming: [],
		months: [{ name: 'January', days: 31 }, { name: 'February', days: 28 }, { name: 'March', days: 31 }, { name: 'April', days: 30 }, { name: 'May', days: 31 }, { name: 'June', days: 30 }, { name: 'July', days: 31 }, { name: 'August', days: 31 }, { name: 'September', days: 30 }, { name: 'October', days: 31 }, { name: 'November', days: 30 }, { name: 'December', days: 31 }],
		days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
		cal: {}
	}

	function loadCalendar() {
		const today = new Date(),
		location = localStorage.location;
		let todayObj;

		todayObj = init({
			date: today.getDate(),
			day: today.getDay(),
			month: today.getMonth(),
			year: today.getFullYear() 
		});

		calendar.today = calendar.cal[todayObj];

		if (location) {
			selectLocation(_.find(cities, city => city['city_id'] == location));
		}
	}

	function selectLocation(location) {
		if (!calendar.calDates[location['city_id']]) {
			import('../assets/cal/location-' + location['city_id'] + '.js')
			  .then(locationImport => {
					loadLocation({ location, locationImport: locationImport.default });
				})
		} else {
			loadLocation({ location });
		}
	}

	function loadLocation({ location, locationImport }) {
		let dateToSelect;

		calendar.index = -1;
		calendar.selectedLocation = location;
		localStorage.location = location['city_id'];

		if (!calendar.calDates[calendar.selectedLocation['city_id']]) {
			calendar.calDates[calendar.selectedLocation['city_id']] = locationImport;
		}

		_.each(calendar.calDates[calendar.selectedLocation['city_id']], (date, dateText) => {
			let dateTextSplit = dateText.split('-'), imgEv;

			date.dateText = dateText;
			date.year = parseInt(dateTextSplit[0]);
			date.month = parseInt(dateTextSplit[1]) - 1;
			date.date = parseInt(dateTextSplit[2]);

			imgEv = _.find(date.events, ev => ev.hasOwnProperty('url'));

			if (imgEv) date.url = imgEv.url;

			if (dateText === calendar.today.dateText) {
				calendar.today = date;
			}
		});

		if (calendar.selectedDate && calendar.selectedDate.date) {
			dateToSelect = calendar.selectedDate;
		} else {
			dateToSelect = calendar.today;
		}

		selectDate(dateToSelect);
	}

	function setMonth(date, dateIndex, monthIndex, year) {
		if (calendar.year !== year) {
			checkFeb();
		}

		calendar.year = year;
		calendar.index = monthIndex;
		calendar.weeks = [];
		calendar.day1Index = date === 1 ? dateIndex : ((dateIndex - (date % 7)) + 1 + 7) % 7;

		fillMonth(0, 1);
	}

	function fillMonth(weekNo, date) {
		let lastDayReached = false,
			params = { todayText: calendar.today.dateText, calDates: calendar.calDates[calendar.selectedLocation['city_id']] },
			itrDate;

		calendar.weeks.push([]);

		for (let i = 0; i < 7; i++) {
			itrDate = { date, month: calendar.index, year: calendar.year, dayIndex: i };
			if ((weekNo == 0 && i < calendar.day1Index) || lastDayReached) {
				calendar.weeks[weekNo].push('');
			} else {
				let appDate = init(itrDate, params);
				calendar.weeks[weekNo].push(appDate);
				if (date === calendar.months[calendar.index].days) {
					lastDayReached = true;
					calendar.dayLastIndex = i;
				}
				date++;
			}
		}
		if (!lastDayReached) {
			fillMonth(weekNo + 1, date);
		}
	}

	function getPrev() {
		let year = calendar.index !== 0 ? calendar.year : calendar.year - 1,
			index = (calendar.index + 11) % 12
	
		setMonth(calendar.months[index].days, (calendar.day1Index + 6) % 7, index, year);
	}

	function getNext() {
		let year = calendar.index !== 11 ? calendar.year : calendar.year + 1;

		setMonth(1, (calendar.dayLastIndex + 1) % 7, (calendar.index + 1) % 12, year);
	}

	function selectDate(date) {
		let prevDate = calendar.selectedDate;

		if (date.dateText && !date.day) date.day = new Date(date.dateText).getDay();

		if (date.month != calendar.index || date.year != calendar.year || calendar.weeks.length < 1) {
			setMonth(date.date, date.day, date.month, date.year);
		}

		if (prevDate.dateText) {
			select(prevDate.dateText);
		}
		
		select(date.dateText);
		calendar.selectedDate = date;
		calendar.isDateSelected = true;
	}

	function checkFeb() {
		if ((calendar.year % 4) === 0) {
			calendar.months[1].days = 29;
		} else {
			calendar.months[1].days = 28;
		}
	}

	function select(dateText) {
		calendar.cal[dateText].selected = calendar.cal[dateText].selected ? false : true;
		setColor(dateText);
	}

	function setColor(dateText) {
		if (calendar.cal[dateText].today) {
			calendar.cal[dateText].bg = 'rgba(255, 165, 0, 0.7)';
		} else if (calendar.cal[dateText].selected) {
			calendar.cal[dateText].bg = 'rgba(255, 165, 0, 0.4)';
		} else {
			calendar.cal[dateText].bg = '';
		}

		if (calendar.cal[dateText].dayIndex === 0) {
			calendar.cal[dateText].color = 'rgb(255, 0, 0)';
		}
	}

	function init(date, params) {
		let dateText = date.dateText || date.year + '-' + ('0' + (date.month + 1)).slice(-2) + '-' + ('0' + date.date).slice(-2);

		if (params) {
			if (params.calDates && params.calDates[dateText]) {
				calendar.cal[dateText] = params.calDates[dateText];
			} else {
				calendar.cal[dateText] = date;
			}
			
			calendar.cal[dateText].today = params.todayText === dateText;
		} else {
			calendar.cal[dateText] = date;
		}
		setColor(dateText);

		calendar.cal[dateText].dateText = dateText;

		return dateText;
	}

  loadCalendar();
</script>
<div class="main">
  <div class="header">
    <div class="monthSelect row flex-md flex-lg shadow-10" v-if="calendar.selectedLocation.name !== 'Select Location...'">
      <div class="row main-center cross-center clickable flex-md flex-lg" on:click={getPrev}>&#9664;</div>
      <div class="row main-center cross-center clickable flex">{ calendar.months[calendar.index].name } { calendar.year }</div>
      <div class="row main-center cross-center clickable flex-md flex-lg" on:click={getNext}>&#9654;</div>
    </div>
    <CalLocationSelect calendar={ calendar } selectLocation={ selectLocation } />
  </div>
  <div class="container" v-if="calendar.selectedLocation.name !== 'Select Location...'">
    <div class="col month">
      <div class="row">
        {#each calendar.days as day, dayIndex}
          <div class="row flex main-center day {dayIndex === 0 ? 'fc-dark-red' : ''}">{ day.substring(0, 3)} </div>
        {/each}
      </div>
      {#each calendar.weeks as week, weekIndex}
        <div class="row flex">
          {#each week as date, dateIndex}
            <div class="row flex">
              {#if calendar.cal[date]}
                <div class="flex row main-center cross-center date clickable"
                  style="background-color: {calendar.cal[date].bg}; color: {calendar.cal[date].color}; --eventBgImg: {calendar.cal[date].url}"
									on:click={() => selectDate(calendar.cal[date])}
                >
									{calendar.cal[date].date}
									{#if calendar.cal[date].ekadashi}
										<div class="dot red row main-center cross-center"></div>
									{/if}
									{#if calendar.cal[date].events && ((calendar.cal[date].events.length > 0) || (calendar.cal[date].special))}
										<div class="dot blue row main-center cross-center">
											{calendar.cal[date].special ? calendar.cal[date].events.length + 1 : calendar.cal[date].events.length}
										</div>
									{/if}
							</div>
              {/if}
            </div>
          {/each}
        </div>
      {/each}
    </div>
		{#if calendar.selectedDate.dateText}
    	<Events calendar={ calendar } selectDate={ selectDate } />
		{/if}
  </div>
</div>
<style lang="scss">
  @import '../assets/css/app.scss';

.main {
  width: 100%;
	padding: 10px;
}

.header {
	display: flex;
}

.monthSelect {
	border-radius: 3px;
	margin-bottom: 20px;
	height: 50px;
}

.container {
	display: flex;
	flex-direction: row;
}

//Desktop + Tablet
@media screen and (min-width: 768px) {
	.header {
		flex-direction: row;
	}

	.month {
		width: 65%;
		padding: 0 20px;
		box-sizing: border-box;
	}

	.container {
		min-height: calc(100vh - 166px);
		max-height: calc(100vh - 166px);
	}
}

//Mobile
@media screen and (max-width: 767px) {
	.header {
		flex-direction: column-reverse;
	}

	.monthSelect > div:nth-child(odd) {
		width: 20%;
		flex: none;
	}

	.container {
		display: flex;
		flex-direction: column;
	}
}

.dark .date {
	background-color: rgba(50, 50, 50, 0.6);
}

.date {
	border-radius: 3px;
	position: relative;
	background-color: rgba(255, 255, 255, 0.6);

	&::after {
		content: " ";
		position: absolute;
		z-index: -1;
		border-radius: 3px;
		background-image: var(--eventBgImg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position-x: center;
		width: 100%;
		height: 100%;
	}
}

.day {
	margin-bottom: 15px;
}

.today {
	background-color: rgba(255, 165, 0, 0.7);

	&:hover {
		background-color: rgba(255, 165, 0, 0.8);
	}
}

.selectedDate {
	&:not(.today) {
		background-color: rgba(255, 165, 0, 0.4);
	}

	.selectedDate:not(.today):hover {
		background-color: rgba(255, 165, 0, 0.5);
	}
}

.dot {
	position: absolute;
	width: 15px;
	height: 15px;
	border-radius: 7.5px;
	margin-top: 30px;

	&.red {
		background-color: #FA5858;
		margin-left: -30px;
	}

	&.blue {
		background-color: goldenrod;
		margin-left: 30px;
		font-size: 12px;
		font-weight: bold;
		color: white;
	}
}

.fc-dark-red {
	// color: darkred;
	color: red;
}

@media screen and (min-width: 768px) {
  .month {
		width: 65%;
		padding: 0 20px;
		box-sizing: border-box;
	}
}

@media screen and (max-width: 767px) {
  .month {
		width: 100%;
	}

	.date {
		min-height: 60px;
	}
}
</style>