<script>
  import cities from '../assets/js/cities';
  export let calendar;
  export let selectLocation;

  let showLocationList = false;
</script>
<div class="locationSelect shadow-10">
  <div class="row cross-center clickable h-100" on:click={ () => { showLocationList = !showLocationList } }>
    <div class="flex">{ calendar.selectedLocation.name }</div>
    <div class="w-10"></div>
  </div>
  {#if showLocationList}
    <div class="cities">
      {#each cities as location}
				<div class="clickable" on:click={ () => {
					selectLocation(location);
					showLocationList = false;
				} }>
					{ location.name }
				</div>
      {/each}
    </div>
  {/if}
</div>
