    {#if day.ekadashi}
      <div class="event clickable not special11">{ day.ekadashi }</div>
    {/if}
    {#if day.events && day.events.length}
      {#each day.events as event}
        <a href={event.link} target="_blank" class="event clickable row cross-center {event.link ? '' : 'not'} {event.link ? 'fw-bold' : ''}">
          {#if event.url}
            <div class="event-image" style={ 'background-image: ' + event.url }></div>
          {/if}
          <div>{ event.name }</div>
        </a>
      {/each}
    {/if}
  </div>
</div>
